'use client';

import React, { useState, useEffect } from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { DayPicker } from 'react-day-picker';
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from '@/components/ui/popover';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';
import { format } from 'date-fns';
import { CalendarIcon } from 'lucide-react';
import { useRouter } from 'next/navigation';
import "react-day-picker/dist/style.css";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "../../../components/ui/table";

interface StudentMealStatus {
  studentName: string;
  breakfast: boolean;
  lunch: boolean;
  dinner: boolean;
}

interface MealAnalytics {
  breakfast: number;
  lunch: number;
  dinner: number;
  totalStudents: number;
  studentStatuses: StudentMealStatus[];
}

export default function Analytics() {
  const [date, setDate] = useState<Date>(new Date());
  const [analytics, setAnalytics] = useState<MealAnalytics>({
    breakfast: 0,
    lunch: 0,
    dinner: 0,
    totalStudents: 0,
    studentStatuses: [],
  });
  const [error, setError] = useState<string>('');
  const router = useRouter();

  const fetchAnalytics = async (selectedDate: Date) => {
    try {
      const hostelId = localStorage.getItem('hostelId');
      const token = localStorage.getItem('token');
      
      if (!hostelId || !token) {
        console.error('Authentication data not found');
        router.push('/login');
        return;
      }

      const formattedDate = format(selectedDate, 'yyyy-MM-dd');
      console.log('Fetching analytics for:', formattedDate, 'hostelId:', hostelId);
      
      const response = await fetch(
        `/api/student-meal-status/analytics/${formattedDate}?hostelId=${hostelId}`,
        {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json',
          },
        }
      );

      if (!response.ok) {
        console.error('API Error:', response.status, await response.text());
        if (response.status === 401) {
          router.push('/login');
          return;
        }
        throw new Error('Failed to fetch analytics');
      }

      const data = await response.json();
      console.log('Analytics data received:', data);
      setAnalytics(data);
      setError('');
    } catch (error) {
      console.error('Error fetching analytics:', error);
      setError('Failed to load analytics data. Please try again.');
    }
  };

  useEffect(() => {
    fetchAnalytics(date);
  }, [date]);

  if (error) {
    return (
      <div className="p-6">
        <div className="text-red-500 bg-red-50 dark:bg-red-900/30 rounded-lg p-4">
          {error}
        </div>
      </div>
    );
  }

  return (
    <div className="p-6">
      <div className="mb-6 flex items-center justify-between">
        <h2 className="text-2xl font-bold">Student Food Table</h2>
        <Popover>
          <PopoverTrigger asChild>
            <Button
              variant="outline"
              className={cn(
                'justify-start text-left font-normal',
                !date && 'text-muted-foreground'
              )}
            >
              <CalendarIcon className="mr-2 h-4 w-4" />
              {date ? format(date, 'PPP') : <span>Pick a date</span>}
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-auto p-0" align="end">
            <DayPicker
              mode="single"
              selected={date}
              onSelect={(newDate) => newDate && setDate(newDate)}
              initialFocus
            />
          </PopoverContent>
        </Popover>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <Card>
          <CardContent className="pt-6">
            <div className="text-center">
              <h3 className="text-lg font-semibold mb-2">Total Breakfast</h3>
              <div className="text-4xl font-bold text-blue-600">
                {analytics.breakfast}
              </div>
              <p className="text-sm text-gray-500 mt-2">students</p>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="pt-6">
            <div className="text-center">
              <h3 className="text-lg font-semibold mb-2">Total Lunch</h3>
              <div className="text-4xl font-bold text-green-600">
                {analytics.lunch}
              </div>
              <p className="text-sm text-gray-500 mt-2">students</p>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="pt-6">
            <div className="text-center">
              <h3 className="text-lg font-semibold mb-2">Total Dinner</h3>
              <div className="text-4xl font-bold text-purple-600">
                {analytics.dinner}
              </div>
              <p className="text-sm text-gray-500 mt-2">students</p>
            </div>
          </CardContent>
        </Card>
      </div>

      <Card>
        <CardContent className="pt-6">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Student Name</TableHead>
                <TableHead className="text-center">Breakfast</TableHead>
                <TableHead className="text-center">Lunch</TableHead>
                <TableHead className="text-center">Dinner</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {analytics.studentStatuses.map((student, index) => (
                <TableRow key={index}>
                  <TableCell className="font-medium">{student.studentName}</TableCell>
                  <TableCell className="text-center">
                    {student.breakfast ? "✅" : "❌"}
                  </TableCell>
                  <TableCell className="text-center">
                    {student.lunch ? "✅" : "❌"}
                  </TableCell>
                  <TableCell className="text-center">
                    {student.dinner ? "✅" : "❌"}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
